/* Container for the hamburger menu */
.hamburger-menu {
    position: relative;
  }
  
  /* Hamburger icon */
  .hamburger-icon {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 22px;
    border: none;
    background: transparent;
    cursor: pointer;
    padding: 0;
    z-index: 2;
  }
  
  .hamburger-icon .line {
    width: 100%;
    height: 3px;
    background-color: #434e39;
    border-radius: 5px;
    transition: transform 0.3s, opacity 0.3s;
  }
  
  /* Menu styling */
  .menu {
    display: none;
    position: fixed;
    top: 10;
    left: 0; /* Align the menu to the right of the hamburger icon */
    width: 15vw;
    height: 100vh; /* Full height of the viewport */
    background-color: #c6f6b1;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 10px;
    border-radius: 5px;
    z-index: 1;
  }
  
  .menu.open {
    display: block;
  }
  
  .menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .menu ul li {
    margin: 10px 0;
  }
  
  .menu ul li a {
    text-decoration: none;
    color: #000;
    font-size: 16px;
  }
  
  .menu ul li a:hover {
    color: #007bff;
  }
  